import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import SquareIcon from '@mui/icons-material/Square';
import Typography from '@mui/material/Typography';


const ColorSelect = ({ setSelectedColor, defaultColor }) => {
  const colorList = [
    "red",
    "orange",
    "yellow",
    "green",
    "tiffany",
    "light-blue",
    "blue",
    "brown",
    "pupple",
    "pink",
    "white",
    "gray",
    "black",
    "texture-black"
  ]
  const colorMap = {
    "red": {
      "color": "red",
      "display_name": ""
    },
    "orange": {
      "color": "orange",
      "display_name": ""
    },
    "yellow": {
      "color": "yellow",
      "display_name": ""
    },
    "green": {
      "color": "green",
      "display_name": ""
    },
    "tiffany": {
      "color": "#81D8D0",
      "display_name": ""
    },
    "light-blue": {
      "color": "light-blue",
      "display_name": ""
    },
    "blue": {
      "color": "blue",
      "display_name": ""
    },
    "brown": {
      "color": "brown",
      "display_name": ""
    },
    "pupple": {
      "color": "pupple",
      "display_name": ""
    },
    "pink": {
      "color": "pink",
      "display_name": ""
    },
    "white": {
      "color": "white",
      "display_name": ""
    },
    "gray": {
      "color": "gray",
      "display_name": ""
    },
    "black": {
      "color": "black",
      "display_name": ""
    },
    "texture-black": {
      "color": "texture-black",
      "display_name": ""
    }
  }
  const [open, setOpen] = React.useState(false);

  const handleSelectButtonClick = (color) => {
    setSelectedColor(color);
    setOpen(false);
  }
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
    }
  };

  return (
    <div>
      <Button variant="text" aria-label={colorMap[defaultColor].display_name} size="large" onClick={handleClickOpen} style={{ color: 'black' }} startIcon={<SquareIcon fontSize="inherit" style={{ color: colorMap[defaultColor].color, boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px' }}/>}>
        <Typography variant="subtitle1">{colorMap[defaultColor].display_name}</Typography>
      </Button>
      <Dialog disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap' }}>
            {colorList.map((colorItem) => (
              <IconButton key={colorItem} aria-label={colorMap[colorItem].display_name} size="large" onClick={() => handleSelectButtonClick(colorItem)}>
                <SquareIcon fontSize="inherit" style={{ color: colorMap[colorItem].color, boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px' }}/>
              </IconButton>
            ))}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ColorSelect;