import Paper from '@mui/material/Card';
import Typography from '@mui/material/Typography'

const ItemPicker = ({ selected, image, text }) => {
  return (
    <Paper variant='outlined' style={{ height: 80, display: 'flex', flexDirection: 'column', alignContent: 'center', alignItems: 'center', backgroundColor: selected? '#2196f3': 'None'}}>
      <image style={{ height: 60, width:'100%'}}/>
      <Typography variant="caption" component="div" color={selected?"white":"text.secondary"}>
          {text}
      </Typography>
    </Paper>
  );
}

export default ItemPicker;